.loading-screen-container{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    .loading-screen{
        img{
            width: 30vw;
            object-fit: cover;
        }
    }
}